
import XHeader from 'vux/src/components/x-header/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Scroll from 'assembly/scroll/scroll'
import Loading from 'assembly/loading/loading'

const PAGESIZE = 20

export default {
data () {
return {
customerArr: [],
listenScroll: true,
pullup: true,
hasMore: true,
inited: false,
pageNo: 1,
phoneNo: '',
items1: [{
key: '7',
value: '近七天'
}, {
key: '30',
value: '一个月'
}, {
key: '180',
value: '半年'
}, {
key: '365',
value: '一年'
}],
arriveStoreArr: [{
key: '1',
value: '已到店'
}, {
key: '0',
value: '未到店'
}],
getBackArr: [{
key: '1',
value: '已领回'
}, {
key: '0',
value: '未领回'
}],
product_srvc_type: [{
key: '',
value: '全部'
}, {
key: '1',
value: '净水器'
}, {
key: '2',
value: '美食教做'
}, {
key: '3',
value: '锅具'
}, {
key: '4',
value: '43件套'
}, {
key: '5',
value: '营养品'
}, {
key: '6',
value: '纤巧壶'
}],
timeRange: {},
proType: {key: '', value: '全部'},
arriveStore: {},
getBack: {},
tip: 0,
tipArr: ['我的客户', '净水器', '美食教做', '锅具服务', '43件套', '营养品', '纤巧壶']
}
},
watch: {
proType (val) {
this.pageNo = 1
this.customerArr = []
this.hasMore = true
this._getCustomerList()
},
timeRange (val) {
this.pageNo = 1
this.customerArr = []
this.hasMore = true
this._getCustomerList()
},
arriveStore (val) {
this.pageNo = 1
this.customerArr = []
this.hasMore = true
this._getCustomerList()
},
getBack (val) {
this.pageNo = 1
this.customerArr = []
this.hasMore = true
this._getCustomerList()
}
},
methods: {
formSubmit () {
return false
},
loadMore () {
if (!this.hasMore) {
return
}
this.pageNo ++
this._getCustomerList()
},
_getCustomerList () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候')
return
}
this.inited = true
let data = {
pagingQuery: {
pageIndex: this.pageNo,
pageSize: PAGESIZE
},
phoneNo: this.phoneNo,
types: this.proType.key ? [parseInt(this.proType.key)] : []
}
let dataUrl = 'client-biz/client-biz/storeClient/clientList'
if (this.tip !== 0) {
data = {
arriveStore: this.arriveStore.key ? parseInt(this.arriveStore.key) : '',
getBack: this.getBack.key ? parseInt(this.getBack.key) : '',
pagingQuery: {
pageIndex: this.pageNo,
pageSize: PAGESIZE
},
phoneNo: this.phoneNo,
timeRange: parseInt(this.timeRange.key),
type: this.tip
}
dataUrl = 'client-biz/client-biz/storeClient/serviceList'
}
this.$tupHttp(this, dataUrl, 'POST', data).then(res => {
this.inited = false
let customerArr = res.data.models.map(item => {

item.createTime = item.createTime ? new Date(item.createTime).format('yyyy-MM-dd hh:mm:ss') : item.createTime
return item
})
if (this.pageNo === 1 && !this.customerArr.length) {
this.customerArr = customerArr
} else {
this.customerArr = this.customerArr.concat(customerArr)
}
if (res.data.models.length < PAGESIZE) { // 是否有下一页
this.hasMore = false
}
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
inputSearch () {
this.pageNo = 1
this.customerArr = []
this._getCustomerList()
},
toAddView () {
this.$router.push({
path: '/customerAdd',
query: {
phoneNo: !this.customerArr.length && this.tip === 0 && this.proType.key === '' ? this.phoneNo : ''
}
})
},
toDetailView (id, tip) {
let type = tip
if (type === 0) { type = this.proType.key ? parseInt(this.proType.key) : 1 }
this.$router.push({
path: '/customerSRV',
query: {
id: id,
type: type
}
})
},
_getDicts () {
let data = ['product_srvc_type']
this.$tupHttp(this, 'front/dict/getDicts', 'POST', data).then(res => {
this.product_srvc_type = res.data.model.product_srvc_type.map((item, index) => {
let sItem = {}
sItem.key = item.dictValue
sItem.value = item.dictName
return sItem
})
this.product_srvc_type.unshift({key: '', value: '全部'})
})
}
},
mounted () {
this.tip = parseInt(this.$route.query.tip)
this.phoneNo = this.$route.query.phoneNo
if (this.tip === 0) {

this._getCustomerList()
} else {
this.timeRange = {key: '30', value: '一个月'}
}
},
components: {
XHeader,
Checker,
CheckerItem,
Scroll,
Loading,
Divider
}
}



